<script>
  import { Card } from '@/components';
  export default {
    components: {
      Card
    },
    data(){
      return  {
        posts: null,
        loading: true,
        error: '',
        numBlogsPerRow: 3,
      }
    },
    created() {
      this.fetchData();
    },

    methods: {
      async fetchData() {
        this.error = null;
        this.post = null;
        this.loading = true;
        try {
          const secret = process.env.VUE_APP_GCMS_TOKEN
          const response = await fetch(
              '' + process.env.VUE_APP_GCMS_ENDPOINT,
              {
                method: 'POST',
                headers: {
                  //'Content-Type': 'application/json'
                  // 'Content-Type': 'application/x-www-form-urlencoded',
                  'Authorization': `Bearer ${secret}`,
                },
                body: JSON.stringify({
                  query: `query GetAllBlogPosts {
  posts {
    title
    excerpt
    coverImage {
      url(
      transformation: {
        image: { resize: { width: 500, fit: clip } }
      }
    )

    }
    author {
      name
      picture {
        url(transformation: {image: {resize: {width: 75, height: 75, fit: clip}}})
      }
    }
    slug
    tags
  }
}`,
                  variables: {
                    slug: this.$route.params.slug,
                  },
                }),
              }
          );
          const { data } = await response.json();
          this.loading = false;
          this.error = data.errors;
          this.posts = data.posts;


          //if(this.posts.length > this.numBlogsPerRow) {
            this.posts = this.changeBlogArray(this.posts, this.numBlogsPerRow);
          //}
          // console.log("error: ");
          // console.log(data.errors);
        }
        catch (e) {
          // handle error

          console.log("catch");
        }
      },
      changeBlogArray: function(arr, size) {
        var newArray = [];
        if (!size) return newArray

        while (arr.length > 0) {
          newArray.push(arr.slice(0, size));
          arr = arr.slice(size);
        }

        return newArray;
      },
    }
  }
</script>
