import Vue from "vue";
import Router from "vue-router";
import StarterPage from "./pages/starter/StarterPage.vue";
import MainNavbar from "./layout/MainNavbar.vue";
import StarterFooter from "./layout/StarterFooter.vue";
import Presentation from "@/pages/Presentation";
import MainFooter from "@/layout/MainFooter";
import Components from "@/pages/Components";
import LandingPage from "@/pages/hcad-pages/LandingPage";
import ContactPage from "@/pages/hcad-pages/ContactPage";
import Blogs from "@/pages/hcad-pages/Blogs";
import Successes from "@/pages/hcad-pages/Successes";
import Sections from "@/pages/Sections";
import Team from "@/pages/hcad-pages/Team";
import HowWeHelp from "@/pages/hcad-pages/HowWeHelp";
import BlogPost from "@/pages/hcad-pages/BlogPost";

Vue.use(Router);

export default new Router({
  mode: 'history',
  routes: [
    {
      path: "/",
      name: "landing",
      components: {default: LandingPage, header: MainNavbar, footer: StarterFooter},
      props: {
        footer: {backgroundColor: 'black'},
        header: {colorOnScroll: 10}
      }
    },
    {
      path: "/how-we-help",
      name: "how-we-help",
      components: {default: HowWeHelp, header: MainNavbar, footer: StarterFooter},
      props: {header: {colorOnScroll: 200}}
    },
    {
      path: "/contact",
      name: "contact",
      components: {default: ContactPage, header: MainNavbar, footer: StarterFooter},
      props: {header: {transparent: false,}}
    },
    {
      path: "/blog",
      name: "blogs",
      components: {default: Blogs, header: MainNavbar, footer: StarterFooter},
      props: {header: {transparent: false}}
    },
    {
      path: "/blog/:slug",
      name: "Blog",
      components: {default: BlogPost, header: MainNavbar, footer: StarterFooter},
      props: {header: {transparent: false}}
    },
    {
      path: "/success-stories",
      name: "successes",
      components: {default: Successes, header: MainNavbar, footer: StarterFooter},
      props: {header: {transparent: false}}
    },
    {
      path: "/team",
      name: "team",
      components: {default: Team, header: MainNavbar, footer: StarterFooter},
      props: {header: {transparent: false}}
    },
    {
      path: "/presentation",
      name: "presentation",
      components: {default: Presentation, header: MainNavbar, footer: MainFooter},
      props: {header: {colorOnScroll: 500}}
    },
    {
      path: "/components",
      name: "components",
      components: {default: Components, header: MainNavbar, footer: MainFooter},
      props: {
        footer: {backgroundColor: 'black'},
        header: {colorOnScroll: 65}
      }
    },
    {
      path: "/sections",
      name: "sections",
      components: {default: Sections, header: MainNavbar, footer: MainFooter},
      props: {
        footer: {backgroundColor: 'gray'},
        header: {colorOnScroll: 0}
      }
    }
  ],
  scrollBehavior: (to, from, savedPosition) => {
    if (to.hash) {
      return {selector: to.hash}
    } else {
      return { x: 0, y: 0 }
    }
  },
});
