<template>
  <div class="wrapper">
    <div class="page-header page-header-small">
      <parallax class="page-header-image"
                style="background-image: url('img/bg32.jpg')">
      </parallax>
      <div class="content-center">
        <div class="row">
          <div class="col-md-8 ml-auto mr-auto">
            <h1 class="title">Contact</h1>
            <h4>Reach out to see how our team can help you with <span>{{financials}}</span></h4>
          </div>
        </div>
      </div>
    </div>
    <contact-us></contact-us>
  </div>
</template>

<script>
import ContactUs from "@/pages/hcad-pages/sections/ContactUs";

export default {
  name: "ContactPage",
  components: {ContactUs},
  data() {
    return {
      financial_strings: ['Capital Markets', 'Mergers and Acquisitions', 'Financial Management'],
    }
  },
  asyncComputed: {
    financials: async function() {
      for (let i =0; i<this.financial_strings.length; i++){
        await this.sleep(1000);
        return this.financial_strings[i];
      }

    },
  },
  methods: {
    sleep: function(ms) {
      return new Promise(resolve => setTimeout(resolve, ms));
    }
  }
}
</script>

